import styled from "styled-components";
import { Icon, Menu } from "src/widgets";
import "./style.scss";
import React, { useEffect, useState } from "react";

const Container = styled.div`
  width: ${(props) =>
    typeof props.width === "string" ? props.width : `${props.width}px`};
`;
export const Search: React.FC<SearchProps> = (props) => {
  const { value, items, width = 400 } = props;

  // state
  const [state, setState] = useState<{ value: string | number }>({
    value: value,
  });

  useEffect(() => {
    setState({ ...state, value });
  }, [value]);

  const onChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    const new_value = event.target.value;
    props.onChange
      ? props.onChange(new_value)
      : setState({ ...state, value: new_value });
  };
  return (
    <Container className="worldie-search">
      {/* width={width}*/}
      {items && (
        <Menu
          menuItems={items}
          activator={
            <div className="icon">
              <Icon name="caret-down" />
            </div>
          }
        />
      )}
      <input type="text" value={state.value || ""} onChange={onChange} />
      <div className="search-icon">
        <Icon name="search" />
      </div>
    </Container>
  );
};

export default Search;
