import React, { useEffect, useMemo, useState } from "react";
import styled from "styled-components";

import "./style.scss";

const Container = styled.div`
  background-color: ${(props) => props.backgroundColor};
  border-radius: ${(props) =>
    typeof props.borderRadius === "string"
      ? props.borderRadius
      : `${props.borderRadius}px`};
`;

export const Input: React.FC<InputProps> = (props) => {
  const {
    value,
    placeholder,
    type = "text",
    borderRadius = 0,
    disabled,
    className,
    backgroundColor = "white",
  } = props;

  // state
  const [state, setState] = useState<{ value: string | number }>({
    value: "",
  });

  useEffect(() => {
    setState({ ...state, value: value || "" });
  }, [value]);

  const class_name = useMemo<string>(() => {
    const class_name = ["worldie-input"];
    disabled && class_name.push("disabled");
    className && class_name.push(className);
    return class_name.join(" ");
  }, [className, disabled]);

  const onChange = (event: React.ChangeEvent<HTMLInputElement>): void => {
    const new_value = event.target.value;
    props.onChange
      ? props.onChange(new_value)
      : setState({ ...state, value: new_value });
  };
  return (
    <Container
      className={class_name}
      borderRadius={borderRadius}
      backgroundColor={backgroundColor}
    >
      <input
        type={type}
        value={state.value}
        onChange={onChange}
        placeholder={placeholder}
      />
    </Container>
  );
};

export default Input;
