import React, { useEffect, useMemo, useState } from "react";
import styled from "styled-components";
import { Icon } from "src/widgets";
import "./style.scss";

const Container = styled.div`
  --worldie-checkbox-color: ${(props) => props.color};
  --worldie-checkbox-label-color: ${(props) => props.labelColor};
`;

export const Checkbox: React.FC<CheckboxProps> = (props) => {
  const {
    checked,
    disabled,
    labelColor = "currentColor",
    color = "white",
    className,
  } = props;

  // state
  const [state, setState] = useState<{
    checked?: boolean;
  }>({
    checked: checked,
  });

  useEffect(() => {
    setState({ ...state, checked });
  }, [checked]);

  const class_name = useMemo<string>(() => {
    const class_name = ["worldie-checkbox"];
    disabled && class_name.push("disabled");
    className && class_name.push(className);
    return class_name.join(" ");
  }, [disabled, className]);

  const onClick = (event: React.MouseEvent): void => {
    props.onChange
      ? props.onChange(!state.checked)
      : setState({ ...state, checked: !state.checked });
    props.onClick && props.onClick(event);
  };
  return (
    <Container
      className={class_name}
      onClick={onClick}
      color={color}
      labelColor={labelColor}
    >
      <div className="icon">
        <Icon name={state.checked ? "checkbox-on" : "checkbox-off"} />
      </div>
      {props.children && <div className="label">{props.children}</div>}
    </Container>
  );
};

export default Checkbox;
