import styled from "styled-components";
import { Icon } from "src/widgets";
import "./style.scss";

const Container = styled.div`
  --project-box-color: ${(props) => props.color};
`;

export const ProjectBox: React.FC<ProjectBoxProps> = (props) => {
  const { icon, title, description, color } = props;
  return (
    <Container className="project-box-component" color={color}>
      <div className="content">
        <div className="icon">
          {typeof icon === "string" ? <Icon name={icon} /> : icon}
        </div>
        <div className="title">{title}</div>
        <div className="description">{description}</div>
      </div>
    </Container>
  );
};
