import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { MenuList } from "./menu-list";
import "./style.scss";

const MenuContainer = styled.li`
  --menu-item-color: ${(props) => props.color};
`;

export const SafeEqualSection: React.FC = () => {
  // state
  const [state, setState] = useState<{
    active: number;
  }>({
    active: 0,
  });

  const [backgroundImage, setBackgroundImage] = useState<string[]>([
    "url(/assets/images/back-01.jpg)",
    "url(/assets/images/back-02.jpg)",
    "url(/assets/images/back-03.jpg)",
    "url(/assets/images/back-04.jpg)",
    "url(/assets/images/back-05.jpg)",
  ]);

  useEffect(() => {
    setTimeout(() => {
      changeBackground();
    }, 3000);
  }, [backgroundImage]);

  const changeBackground = () => {
    const temp = [...backgroundImage];
    const pop = temp.splice(0, 1);
    const new_background = temp.concat(pop);
    setBackgroundImage(new_background);
  };

  return (
    <section
      className="safe-equal"
      style={{ backgroundImage: backgroundImage.join(",") }}
    >
      <div className="container">
        <div className="title">Safe and Equal World</div>
        <div className="sec-tech-good-div mt-0">
          <ul>
            {MenuList.map((item, index) => (
              <MenuContainer
                className={`menu-item${
                  state.active === index ? " active" : ""
                }`}
                color={item.color}
                key={index}
                onClick={() => setState({ ...state, active: index })}
              >
                <div className="content">{item.name}</div>
              </MenuContainer>
            ))}
          </ul>
          {React.createElement(MenuList[state.active].component)}
          <div className="live-tip">
            <div className="image"></div>
            <div className="label">Live a Better Quality of Life</div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default SafeEqualSection;
